import { useContext } from 'react';
import { Link } from 'react-router-dom';

import style from './Header.module.css';
import logo from '../assets/images/logo_site.png';

import LangContext from '../context/lang';

const Header = () => {
    const { lang, setLang } = useContext(LangContext);

    const navElements = [
        { href: '/', en: 'MAIN', ru: 'ГЛАВНАЯ' },
        { href: '/parish', en: 'PARISH', ru: 'ПРИХОД' },
        { href: '/rector', en: 'RECTOR', ru: 'НАСТОЯТЕЛЬ' },
        { href: '/sisterhood', en: 'SISTERHOOD', ru: 'СЕСТРИЧЕСТВО' },
        { href: '/schedule_liturgy', en: 'SCHEDULE', ru: 'РАСПИСАНИЕ' },
        { href: '/orthodox_faith', en: 'ORTHODOX FAITH', ru: 'О ПРАВОСЛАВИИ' },
        { href: '/contacts', en: 'CONTACTS', ru: 'КОНТАКТЫ' },
    ];

    const texts = {
        donate: {
            en: 'DONATE',
            ru: 'ПОЖЕРТВОВАНИЕ',
        }
    }
    const langStyles = {};
    for (const langKey of ['en', 'ru']) {
        langStyles[langKey] = lang === langKey ? style.ActiveLang : style.InactiveLang;
    }

    return (
        <header>
            <div className={style.HeaderTop}>
                <div className={style.HeaderTop__Box}>
                    <Link to='/' className={style.HeaderTop__Brand}>
                        <img src={logo} alt='Holy Trinity Russian Orthodox Church' />
                    </Link>
                    <nav className={style.HeaderTopNav}>
                        <ul className={style.HeaderTopNav__Items}>
                            {navElements.map(element => (
                                <li>
                                    <Link className={style.HeaderTopNav__Item} to={element.href}>{element[lang]}</Link>
                                </li>
                            ))}
                        </ul>
                    </nav>
                </div>
            </div>
            <div className={style.HeaderBottom}>
                <div className={style.HeaderBottom__Box}>
                    <Link to='/parish/support_us' className={style.SupportUs}>{texts['donate'][lang]}</Link>
                    <button className={`${style.Languages} ${langStyles['ru']}`} onClick={() => setLang('ru')}>RU</button>
                    <button className={`${style.Languages} ${langStyles['en']}`} onClick={() => setLang('en')}>EN</button>
                </div>
            </div>
        </header>
        )}

export default Header;